import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import ProfileForm from './form';
import { fetchProfile } from '../../../../firebase/users/profile/profileFetch';
import {useTheme} from '@material-ui/core'
const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function ProfileUpdate(props) {
  const classes = useStyles();
  const [profile,setProfile]=useState({})
  const theme=useTheme()
  fetchProfile(props.uid,setProfile)
  return (
    <div>
      <Dialog fullScreen open={props.open} onClose={props.handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar} style={{backgroundColor:theme.palette.secondary.dark}}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={props.handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Profile Update of {props.email?props.email.slice(0,10)+"...":"No record found"}
            </Typography>
          </Toolbar>
        </AppBar>
        <br/>
        <br/>
        <div style={{marginTop:20}}>
        <ProfileForm {...props} />
        </div>
      </Dialog>
    </div>
  );
}
