import 'date-fns';
import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { CircularProgress,TextField,Select,MenuItem } from '@material-ui/core';
import { faircopyUpdate } from '../../../../firebase/admin/users/attendandresult';
import { toast } from 'react-toastify';
import {useTheme} from '@material-ui/core'
export default function FaircopyDialog(props) {
  const {open,setOpen}=props
  const [selectedDate, setSelectedDate] = React.useState(new Date(Date.now()));
  const [filter,setFilter]=React.useState("HS")
  const [loading,setLoading]=React.useState(false)
  const [topics,setTopics]=React.useState("")
  const theme=useTheme()
  const handleDateChange = date => {
    setSelectedDate(date);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleSubmit=async()=>{
      const {id,setError,email}=props
      let chapters=topics.split(',')
      setLoading(true)
      let res=await faircopyUpdate(id,email,chapters,Date.parse(selectedDate),filter)
      if(res.status===200){
        toast.success(res.message)
        setSelectedDate(new Date(Date.now()))
        setFilter("HS")
        setTopics("")
      }else{
          setError(res.message)
      }
      setLoading(false)
  }

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Enter Faircopy not submitted {props.email&&props.email.slice(0,10)}...</DialogTitle>
        <DialogContent>
          <TextField type="text" value={topics} placeholder="Enter the chapters" onChange={(e)=>{
              setTopics(e.target.value)
          }}/>
          <br/>
          <p>Please do remmember to write multiple chapters name ',' seperated </p>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                margin="normal"
                id="date-picker-dialog"
                label="Date picker dialog"
                format="dd/MM/yyyy"
                value={selectedDate}
                onChange={handleDateChange}
                KeyboardButtonProps={{
                    'aria-label': 'change date',
                }}
                />
            </MuiPickersUtilsProvider>
            <br/>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={filter}
            onChange={(e)=>{
                setFilter(e.target.value)
            }}
            >
            <MenuItem value={"HS"}>HS</MenuItem>
            <MenuItem value={"JOINT"}>JOINT</MenuItem>
        </Select>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          {!loading?<Button onClick={handleSubmit} color="primary" autoFocus>
            Upload
          </Button>:<CircularProgress style={{color:theme.palette.secondary.light,fontSize:20}}/>}
        </DialogActions>
      </Dialog>
    </div>
  );
}


