import React,{useState} from 'react'
import clsx from 'clsx';
import { lighten, makeStyles,fade } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Search from '../common/search'
import {useTheme} from '@material-ui/core'
import Cookie from 'js-cookie'
import { url } from '../../../url';
const useToolbarStyles = makeStyles(theme => ({
    root: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(1),
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.secondary.light, 0.15),
        '&:hover': {
          backgroundColor: fade(theme.palette.secondary.light, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
          marginLeft: theme.spacing(3),
          width: 'auto',
        },
      },
      searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      inputRoot: {
        color: 'inherit',
        width:"100%"
      },
      inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
          width: '20ch',
        },
      },
    highlight:
      theme.palette.type === 'light'
        ? {
            color: theme.palette.secondary.main,
            backgroundColor: lighten(theme.palette.secondary.light, 0.85),
          }
        : {
            color: theme.palette.text.primary,
            backgroundColor: theme.palette.secondary.dark,
          },
    title: {
      flex: '1 1 100%',
    },
  }));
  const handleDownload=()=>{
    let jwt=Cookie.get('userjwt')
    window.open(url+"/csvfile/userInfo/"+jwt,"Downloading the csv file from server")
  }

export default function EnhancedTableToolbar(props){
    const classes = useToolbarStyles();
    const { numSelected,deleteUsers } = props;
    const theme=useTheme()
    return (
      <React.Fragment>
      <Toolbar
        className={clsx(classes.root, {
          [classes.highlight]: numSelected > 0,
        })}
      >
        {numSelected > 0 ? (
          <Typography className={classes.title} color="inherit" variant="subtitle1">
            {numSelected} selected
          </Typography>
        ) : (
          <Typography className={classes.title} variant="h6" id="tableTitle">
            Users list
          </Typography>
        )}
        {/* <Button color="primary" variant="contained" onClick={()=>{
          search()
        }}>Search</Button> */}
        {numSelected > 0 ? (
          <Tooltip title="Delete">
            <IconButton aria-label="delete" onClick={()=>{
              deleteUsers()
            }}>
              <DeleteIcon style={{color:theme.palette.error.main}}/>
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title="Download Excel file">
            <IconButton aria-label="filter list" onClick={()=>{
              handleDownload()
            }}>
                <i className="fas fa-download" style={{color:"blue"}}></i>
            </IconButton>
          </Tooltip>
        )}
      </Toolbar>
        <Search {...props}/>
      </React.Fragment>
    );
  };


  
  