import React,{useEffect,useContext} from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import Home from './pages/commonUser/home';
import Login from './pages/auth/login';
import jscookie from 'js-cookie'
import {AuthContext } from './context/authContext';
import UserCreation from './pages/admin/users/userCreation';
import 'react-toastify/dist/ReactToastify.css';
import AboutUs from './pages/commonUser/aboutus';
import NoticeUpload from './pages/admin/notice/noticeUpload';
import AllNotices from './pages/admin/notice/allNotices';
import AllNoticesUsers from './pages/users/notice/AllNotices';
import Profile from './pages/common/profile/profile';
import Axios from 'axios';
import { url } from './url';
import UserPrefference from './pages/admin/users/userPrefference';
import GalleryUpload from './pages/admin/gallery/galleryUpload';
import AllGallery from './pages/commonUser/allGallery';
import PaymentDone from './pages/admin/payment';
import UserPaymentInfo from './pages/admin/payment/userPaymentInfo';
import MonthLyDone from './pages/admin/payment/monthlyDone';
import UserList from './pages/admin/users/userList';
import Videoes from './pages/commonUser/videoes';
import VideosUpload from './pages/admin/videos/videosUpload';
import AttendAndResult from './pages/admin/users/attendandresult';
import ProfileSingleUser from './pages/common/profile';
import ContactUs from './pages/commonUser/conatctus/contactus';
import ReviewSection from './pages/commonUser/review';
import { firebaseapp } from './firebase/init';
import AllReviews from './pages/admin/review/allreviews';
import WbjeeQuestionUpload from './pages/admin/exam/wbjee';
import JeeMainQuestionUpload from './pages/admin/exam/jeemain';
import HsQuestionUpload from './pages/admin/exam/hs'
import AllJeemainQuestions from './pages/admin/exam/jeemain/allQuestions';
import AllWbjeeQuestions from './pages/admin/exam/wbjee/allQuestions';
import FAQ from './pages/commonUser/faq';
import Courses from './pages/commonUser/courses';
import ScoreBoard from './pages/users/exam/scorecard';
import NotFound404 from './component/shared/404page';
import AllHsQuestions from './pages/admin/exam/hs/allQuestions';

function App() {
  useEffect(()=>{
    checkAdmin()
  },[])
  const [_,setAuth]=useContext(AuthContext)
  async function checkAdmin(){
    const jwt=jscookie.get('userjwt')
    let resp={status:400}
    console.log(jwt)
    if(jwt){
      resp=await Axios.get(url+"/auth/checkJWT/"+jwt).then(res=>{
        return {
          status:res.status,
          data:res.data
        }
      }).catch(err=>{
        return{
          status:400,
          message:err.message
        }
      })
    }
    let isLoggedIn=false,isAdmin=false
    if(resp.status===200)
    {
      if(resp.data.message==="Valid")
      {
        if(resp.data.admin)
        {
          isAdmin=true
        }
        isLoggedIn=true
      }
    }else{
        try{
        let id=jscookie.get("uid")
        if(id){
          await firebaseapp.firestore().collection('users').doc(id).update({
            isLoggedIn:false
          })
        }
      }catch(Err){
        console.log(Err)
      }
    }
    const authTemp={
      isAdmin,
      isLoggedIn
    }
    setAuth(authTemp)
  }
  return (
    <Router>
    <Switch>
    <Route path="/" exact>
      <Home/>
    </Route>
    <Route path="/login" exact>
      <Login/>
    </Route>
    <Route path="/aboutus" exact>
      <AboutUs/>
    </Route>
    <Route path="/allGalleries" exact>
      <AllGallery/>
    </Route>
    <Route path="/videos" exact>
      <Videoes/>
    </Route>
    <Route path="/contactus" exact>
      <ContactUs/>
    </Route>
    <Route path="/review" exact>
      <ReviewSection/>
    </Route>
    <Route path="/faq" exact>
      <FAQ/>
    </Route>
    <Route path="/courses" exact>
      <Courses/>
    </Route>
    {/* common admin and user starts here*/}
    <Route path="/profileSingle" exact>
      <ProfileSingleUser/>
    </Route>
    <Route path="/userprofile" exact>
      <Profile/>
    </Route>
    {/* common admin and user end here */}
    
    {/*Admin routes starts here*/}
    {/*Admin user section starts here*/}
    <Route path="/userCreation" exact>
      <UserCreation/>
    </Route>
    <Route path="/userPrefference" exact>
      <UserPrefference/>
    </Route>
    <Route path="/userList" exact>
      <UserList/>
    </Route>
    <Route path="/userAttendenceResult" exact>
      <AttendAndResult/>
    </Route>
    {/*Admin user section ends here*/}
    {/*Admin notice section starts here*/}
    <Route path="/noticeUpload" exact>
      <NoticeUpload/>
    </Route>
    <Route path="/allNotices" exact>
      <AllNotices/>
    </Route>
    {/*Admin notice section ends here*/}
    {/* Admin gallery section starts here */}
    <Route path="/galleryUpload" exact>
      <GalleryUpload/>
    </Route>
    {/* Admin gallery section ends here */}
    {/* Admin video section starts here */}
    <Route path="/videoUpload" exact>
      <VideosUpload/>
    </Route>
    {/* Admin Video upload section ends here */}
    {/*Admin payment section starts here */}
    <Route path="/userPaymentInfo" exact>
      <UserPaymentInfo/>
    </Route>
    <Route path="/paymentInfo" exact>
      <PaymentDone/>
    </Route>
    <Route path="/monthlyDone" exact>
      <MonthLyDone/>
    </Route>
    {/* Admin payment section ends here */}
    {/* Admin Review section starts here */}
    <Route path="/allReviews" exact>
      <AllReviews/>
    </Route>
    {/* Admin Review section ends here */}
    {/* Admin exam section starts here */}
    <Route path="/wbjeequestionupload" exact>
      <WbjeeQuestionUpload/>
    </Route>
    <Route path="/jeemainquestionupload" exact>
      <JeeMainQuestionUpload/>
    </Route>
    <Route path="/hsquestionupload" exact>
      <HsQuestionUpload/>
    </Route>
    <Route path="/jeemainquestions" exact>
      <AllJeemainQuestions/>
    </Route>
    <Route path="/wbjeequestions" exact>
      <AllWbjeeQuestions/>
    </Route>

    <Route path="/hsquestions" exact>
      <AllHsQuestions/>
    </Route>
    {/* Admin exam section ends here */}
    {/*User's routes starts here*/}
    <Route path="/allNoticesUsers" exact>
      <AllNoticesUsers/>
    </Route>
    <Route path="/scoreboard" exact>
      <ScoreBoard/>
    </Route>
    {/* User's route ends here */}
    {/* 404 notfound page */}
    <Route component={NotFound404}/>
  </Switch>
  </Router>
  );
}


export default App;
