import React,{useState,useContext,useEffect} from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { AuthContext } from '../../context/authContext';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import {Link} from 'react-router-dom'
import { Button } from '@material-ui/core';
import { Row,Col } from 'reactstrap';
import {useHistory} from 'react-router-dom'
import { firebaseapp } from '../../firebase/init';
import CommonUserNavBar from './commonNavBar';
import {logout} from '../util/logout'
const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2)
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor:"#3f51b5"
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
}));
//All Styles are done here
const iconStyle={fontSize:24,color:"#ffffff"}
const textStyle={textDecoration:'none',color:'#ffffff'}
const dividerStyle={color:"#ffffff",backgroundColor:"#ffffff"}

export default function BaseLayout(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [auth,setAuth]=React.useContext(AuthContext)
  const [disableProfile,setDisableProfile]=useState(true)
  const history=useHistory()
  // const handleDrawerOpen = () => {
  //   setOpen(true);
  // };
  useEffect(()=>{
      checkProfileButton()
  },[props.uid])
  const checkProfileButton=async()=>{
    const {uid}=props
      if(uid){
        try{
          let res=await firebaseapp.firestore().collection('users').doc(uid).get()
          if(res.exists){
            if(res.data().name.length>0){
            }else{
              console.log("Hiiiiiii")
              setDisableProfile(false)
            }
          }else{
          }
        }catch(Err){
        }
      }else{
      }
  }
  const toggleDrawer = (anchor, open) => (event) => {
    // if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
    //   return;
    // }
    // console.log("Hiii")
    setOpen(open)
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

if(!auth.isAdmin&&!auth.isLoggedIn){
  return(
    <CommonUserNavBar/>
  );
}
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar style={{justifyContent:'space-between'}}>
        <Row>
          <IconButton
            aria-label="open drawer"
            onClick={toggleDrawer("left",true)}
            edge="start"
            style={{marginLeft:10}}
            className={clsx(classes.menuButton, open && classes.hide)}
          >
            <MenuIcon style={{color:"#ffffff"}}/>
          </IconButton>
          <Typography variant="h6" style={{marginTop:7}}>
            Asim Math
          </Typography>
          {auth.isAdmin&&<Button color="secondary"
            size="small"
            style={{marginLeft:10}}
            onClick={()=>{
              logout(setAuth)
              history.push("/login")
            }}
            variant="contained"
          >Logout</Button>}
          </Row>
          {/* <Row style={{width:"50%"}}>
          <Col style={{padding:5}}>
          {(!disableProfile|| auth.isAdmin)&&props.profileButton&&<Button color="secondary" variant="contained" onClick={()=>{
            props.handleOpen()
          }} className={clsx(classes.menuButton, open && classes.hide)}>Edit Profile</Button>}
          </Col>
          <Col style={{padding:5}}>
          {props.profileButton&&<Button color="primary" variant="contained" onClick={()=>{
              jscookie.set("profileuid",props.uid)
              history.push('/profileSingle')
          }} className={clsx(classes.menuButton, open && classes.hide)}>More Info</Button>}
          </Col>
        </Row> */}
        </Toolbar>
      </AppBar>
      <SwipeableDrawer
        className={classes.drawer}
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
        onOpen={toggleDrawer("left",true)}
        onClose={toggleDrawer("left", false)}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose} style={{backgroundColor:"#ffffff",fontSize:25}}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider style={dividerStyle}/>
        <Link to="/" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-home" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/aboutus" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-address-card" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="About" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        {/* Admin Section starts here */}
        {auth.isAdmin&&<Admin/>}
        <Divider style={dividerStyle}/>
        {/* Admin section ends here */}
        {/* User section starts here */}
        {(auth.isLoggedIn&&!auth.isAdmin)&&<UserSection/>}
        <Divider style={dividerStyle}/>
        {/* User section ends here */}
        <a href="/practice/" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fa fa-question-circle" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Practice Section" />
            </ListItem>
        </List></a>
        <Divider style={dividerStyle}/>
        <Link to="/allGalleries" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-image" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Gallery section" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/videos" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-video" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Video Section" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/contactus" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="far fa-address-book" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Contact Us" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/review" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-comments" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Review Us" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/faq" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-question" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Faq" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        <Link to="/courses" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-graduation-cap" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="Courses" />
            </ListItem>
        </List></Link>
        <Divider style={dividerStyle}/>
        {!auth.isLoggedIn?<Link to="/login" style={{textDecoration:"none"}}>
          <Button 
          style={{backgroundColor:"#ffffff",color:"#3f51b5",
          justifyContent:"center",width:"80%",marginLeft:"10%",marginTop:10}}>Log in</Button>
          </Link>:<Link to="/" style={textStyle}>
          <List>
              <ListItem button onClick={()=>{
                logout(setAuth)
              }}>
                <ListItemIcon><i className="fas fa-sign-out-alt" style={iconStyle}></i></ListItemIcon>
                <ListItemText primary="Log Out"/>
              </ListItem>
          </List>
          </Link>}
      </SwipeableDrawer>

      <div style={{height:64}}>

      </div>
    </div>
  );
}

const UserSection=()=>{ 
  return(
      <div>
          <Link to="/allNoticesUsers" style={textStyle}>
          <List>
              <ListItem button>
                <ListItemIcon><i className="far fa-file-word" style={iconStyle}></i></ListItemIcon>
                <ListItemText primary="All Notices"/>
              </ListItem>
          </List>
          </Link>
          <Divider style={dividerStyle}/>
          <Link to="/userprofile" style={textStyle}>
          <List>
              <ListItem button>
                <ListItemIcon><i className="fas fa-id-badge" style={iconStyle}></i></ListItemIcon>
                <ListItemText primary="Profile"/>
              </ListItem>
          </List>
          </Link>
          <Divider style={dividerStyle}/>
          <a href="/exam/home" style={textStyle}>
          <List>
              <ListItem button>
                <ListItemIcon><i className="fas fa-question" style={iconStyle}></i></ListItemIcon>
                <ListItemText primary="Exam Section"/>
              </ListItem>
          </List>
          </a>
        </div>
      )
}

const Admin=()=>{
    const [userOpen,setUserOpen]=useState(false)
    const [noticeOpen,setNoticeOpen]=useState(false)
    const [paymentOpen,setPaymentOpen]=useState(false)
    const [examOpen,setExamOpen]=useState(false)
    return(
        <div>
            {/*User Section*/}
            <Link to="/userList" style={textStyle}><List>
            <ListItem button>
              <ListItemIcon><i className="fas fa-address-card" style={iconStyle}></i></ListItemIcon>
              <ListItemText primary="UserList" />
            </ListItem>
          </List></Link>
          <Divider style={dividerStyle}/>
          <Link to="/userCreation" style={textStyle}>
            <List component="div" disablePadding>
                <ListItem button>
                <ListItemIcon><i className="fas fa-address-card" style={iconStyle}></i></ListItemIcon>
                  <ListItemText primary="UserCreation" />
                </ListItem>
              </List></Link>
          <Divider style={dividerStyle}/>
           <ListItem button onClick={()=>{
             setUserOpen(!userOpen)
           }}>
              <ListItemIcon>
              <i className="fa fa-users" style={iconStyle}></i>
              </ListItemIcon>
              <ListItemText primary="User Section" style={textStyle}/>
              {userOpen ? <ExpandLess style={iconStyle}/> : <ExpandMore style={iconStyle}/>}
            </ListItem>
            <Collapse in={userOpen} timeout="auto" unmountOnExit>
              <Link to="/userPrefference" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Update User Prefference"/>
                  </ListItem>
              </List>
              </Link>
              <Link to="/userAttendenceResult" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Attendence & Result"/>
                  </ListItem>
              </List>
              </Link>
            </Collapse>
            <Divider style={dividerStyle}/>
            {/*Notice Section*/}
            <ListItem button onClick={()=>{
             setNoticeOpen(!noticeOpen)
           }}>
              <ListItemIcon>
              <i className="far fa-file-word" style={iconStyle}></i>
              </ListItemIcon>
              <ListItemText primary="Notice Section"  style={textStyle}/>
              {noticeOpen ? <ExpandLess style={iconStyle}/> : <ExpandMore style={iconStyle}/>}
            </ListItem>
            <Collapse in={noticeOpen} timeout="auto" unmountOnExit>
              <Link to="/noticeUpload" style={textStyle}><List component="div" disablePadding>
                <ListItem button>
                  <ListItemText primary="Notice Upload" />
                </ListItem>
              </List></Link>
              <Link to="/allNotices" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="All Notices" />
                  </ListItem>
              </List></Link>
            </Collapse>
            <Divider style={dividerStyle}/>
            {/*Gallery section starts here*/}
            <Link to="/galleryUpload" style={textStyle}>
            <List>
                <ListItem button>
                  <ListItemIcon><i className="fas fa-image" style={iconStyle}></i></ListItemIcon>
                  <ListItemText primary="Gallery Upload"/>
                </ListItem>
            </List>
            </Link>
            <Divider style={dividerStyle}/>
            {/*Gallery section ends here*/}
            {/* Video section stars here */}
            <Link to="/videoUpload" style={textStyle}>
            <List>
                <ListItem button>
                  <ListItemIcon><i className="fas fa-video" style={iconStyle}></i></ListItemIcon>
                  <ListItemText primary="Video Upload"/>
                </ListItem>
            </List>
            </Link>
            {/* Video section ends here */}
            {/* Payment related all things starts here */}
            <Divider style={dividerStyle}/>
            <ListItem button onClick={()=>{
             setPaymentOpen(!paymentOpen)
           }}>
              <ListItemIcon>
              <i className="fas fa-shopping-cart" style={iconStyle}></i>
              </ListItemIcon>
              <ListItemText primary="Payment Section"  style={textStyle}/>
              {paymentOpen ? <ExpandLess style={iconStyle}/> : <ExpandMore style={iconStyle}/>}
            </ListItem>
            <Collapse in={paymentOpen} timeout="auto" unmountOnExit>
              <Link to="/userPaymentInfo" style={textStyle}><List component="div" disablePadding>
                <ListItem button>
                  <ListItemText primary="User's payment Info" />
                </ListItem>
              </List></Link>
              <Link to="/paymentInfo" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="All Payments" />
                  </ListItem>
              </List></Link>
              <Link to="/monthlyDone" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Monthly Info" />
                  </ListItem>
              </List></Link>
            </Collapse>
            {/* Payment related all things ends here */}
            {/* Exam section starts here */}
            <Divider style={dividerStyle}/>
            <ListItem button onClick={()=>{
             setExamOpen(!examOpen)
           }}>
              <ListItemIcon>
              <i className="fas fa-question" style={iconStyle}></i>
              </ListItemIcon>
              <ListItemText primary="Exam Section"  style={textStyle}/>
              {examOpen ? <ExpandLess style={iconStyle}/> : <ExpandMore style={iconStyle}/>}
            </ListItem>
            <Collapse in={examOpen} timeout="auto" unmountOnExit>
              <Link to="/wbjeequestionupload" style={textStyle}><List component="div" disablePadding>
                <ListItem button>
                  <ListItemText primary="Wbjee Question Upload" />
                </ListItem>
              </List></Link>
              <Link to="/wbjeequestions" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Wbjee questions" />
                  </ListItem>
              </List></Link>
              <Link to="/jeemainquestionupload" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="JeeMain Question Upload" />
                  </ListItem>
              </List></Link>
              <Link to="/jeemainquestions" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="JeeMain Questions" />
                  </ListItem>
              </List></Link>
              <Link to="/hsquestionupload" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Hs Question Upload" />
                  </ListItem>
              </List></Link>
              <Link to="/hsquestions" style={textStyle}><List>
                <ListItem button>
                    <ListItemText primary="Hs Questions" />
                  </ListItem>
              </List></Link>
            </Collapse>
          <Divider style={dividerStyle}/>
          <Link to="/scoreboard" style={textStyle}>
          <List>
              <ListItem button>
                <ListItemIcon><i className="fas fa-id-badge" style={iconStyle}></i></ListItemIcon>
                <ListItemText primary="Online exam scorboard"/>
              </ListItem>
          </List>
          </Link>
            {/* Exam section ends here */}
            <Divider style={dividerStyle}/>
            <Link to="/allReviews" style={textStyle}>
            <List>
                <ListItem button>
                  <ListItemIcon><i className="fas fa-comments" style={iconStyle}></i></ListItemIcon>
                  <ListItemText primary="Review section"/>
                </ListItem>
            </List>
            </Link>
        </div>
    );
}
